.footer {
  width: 100%;
  min-width: 1024px;
  height: 180px;
  border-top: 1px solid black;
  background-color: #ffffff;
}
.footerContainer {
  padding-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.footerContainer-1 {
  display: flex;
  flex-direction: column;
}
.footerInfo {
  padding-top: 20px;
  padding-bottom: 20px;
  width: 1400px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.footerBottom {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  color: #ffffff;
  overflow: hidden;
}
@media (max-width: 1440px) {
  .footerContainer-1 {
    width: 100%;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
  }
  .footerInfo {
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
  }
  .footerInfo > div {
    margin-top: 3px;
  }
}
@media (max-width: 1024px) {
  .footerContainer-1 {
    width: 100%;
    max-width: 900px;
    display: flex;
    flex-direction: column;
  }
}
