.admin-container {
  width: 1500px;
  margin: 0 auto;
}

.admin-left {
  width: 244px;
  height: calc(100vh - 70px);
}

.admin-nav {
  width: 200px;
  margin-top: 30px !important;
}

.admin-nav nav {
  box-sizing: border-box;
  color: #fff;
  width: 100%;
  height: 50px;
  text-align: center;
  padding: 14px;
  cursor: pointer;
}
@keyframes nav-slide {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.admin-tech {
  display: flex;
  position: relative;
  flex-direction: column;
  border-left: 0.75px solid #244497;
  border-right: 1px solid #244497;
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease-in-out;
}

.admin-tech-show {
  height: auto;
}

.admin-tech-hide {
  display: none;
  transition: 1s;
}

.admin-tech-nav {
  background-color: #fff;
  color: #244497 !important;
  transition: background 0.2s ease-in-out;
}

.admin-tech-nav:hover {
  background-color: #dadada;
}

.admin-content {
  width: 100%;
  /* height: 100%; */
}

.admin-content-header {
  color: #fff;
  box-sizing: border-box;
  padding: 20px 0 20px 42px;
}

.admin-notice-register {
  margin: 10px 0 10px 55px;
  width: 90px;
  height: 45px;
  color: #fff;
  cursor: pointer;
}

.admin-notice-register:hover {
  background-color: #3f5998;
}

.admin-content-main {
  border: 0.75px solid #000;
  height: 750px;
  margin: 30px 55px;
}

/* .admin-content-list {
  border-bottom: 0.75px solid #000;
  width: 100%;
  height: 44px;
  padding-top: 11px;
  box-sizing: border-box;
} */

.admin-content-number {
  width: 60px;
  height: 100%;
  text-align: center;
}

.admin-content-title {
  width: 80%;
}

.admin-content-date {
  width: 200px;
  text-align: center;
  justify-content: center;
}

.admin-register-container {
  margin: 0 55px;
  height: 850px;
}

.admin-register-title-input input {
  margin: 10px 0 20px 0;
  width: 100%;
  height: 45px;
  padding: 8px;
  font-size: 16px;
  box-sizing: border-box;
}

.admin-register-content {
  margin: 10px 0;
  width: 100%;
  padding: 10px;
  height: 750px;
  resize: none;
  font-size: 1rem;
  box-sizing: border-box;
}

.admin-register-btn {
  width: 100%;
  text-align: right;
  margin-top: 10px;
  margin-right: 55px;
  float: right;
}

.admin-product-list {
  border-bottom: 0.75px solid #000;
  width: 100%;
  height: 130px;
  padding: 5px;
  box-sizing: border-box;
}

.admin-content-img img {
  width: 187px;
  height: 120px;
}

.admin-product-number {
  width: 60px;
  height: 100%;
  margin-top: 44px;
  text-align: center;
}

.admin-product-title {
  padding: 44px 0;
  box-sizing: border-box;
  margin-left: 20px;
  font-size: 20px;
  width: 80%;
}

.admin-content-lists {
  height: 150px;
}

.admin-content-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 13px;
  border-bottom: 1px solid #ccc;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

.admin-content-list:hover {
  background: #eaeaea;
}

.admin-content-number {
  width: 5%;
  text-align: center;
  font-weight: bold;
  color: #555;
}

.admin-content-title {
  width: 70%;
  padding-left: 1rem;
  color: #333;
}

.admin-content-date {
  width: 25%;
  text-align: right;
  color: #999;
}

.admin-header {
  height: 80px;
  padding: 12px 0 0 0;
  box-sizing: border-box;
  background: #f1f1f1;
  border-bottom: 3px solid #244497;
}

.admin-header-container {
  width: 1500px;
  margin: 0 auto;
}

.admin-header-logo > img {
  height: 49px;
  object-fit: contain;
}

.admin-header-title {
  min-width: 200px;
  height: 40px;
  margin-top: 5px;
  margin-left: 33px;
  font-size: 24px;
  font-weight: 500;
  text-align: center;
  padding: 5px;
  box-sizing: border-box;
  background: #244497;
  color: #fff;
  border-radius: 10px;
}

.admin-notice-box {
  margin: 30px 36px;
  padding: 30px 75px;
  box-sizing: border-box;
  width: 1250px;
  height: 900px;
  background: #f1f1f1;
}

.admin-notice-box input {
  width: 1100px;
  border: 1.5px solid black;
  padding: 10px 10px 10px 20px;
  box-sizing: border-box;
  font-size: 1.25rem;
  font-weight: 500;
}

.notice-content {
  width: 1100px;
  border: 1.5px solid black;
  padding: 20px;
  box-sizing: border-box;
  font-size: 1.25rem;
  font-weight: 500;
  margin-top: 40px;
  height: 660px;
}

.notice-save {
  width: 150px;
  margin: 25px auto;
}

.notice-btn {
  font-size: 1.25rem;
  font-weight: 500;
  border-radius: 10px;
  border: none;
  color: #fff;
  width: 130px;
  height: 45px;
  background: #244497;
  cursor: pointer;
}

.notice-btn:hover {
  background: #3f61b7;
  transform: translateY(-2px);
}

.admin-detail-container {
  width: 1024px;
  min-height: 500px;
  background-color: #efefef;
  margin: 30px 55px;
  padding: 30px;
  box-sizing: border-box;
}

.admin-detail-title {
  font-size: 1.8rem;
  margin-bottom: 10px;
}

.admin-detail-box {
  width: 900px;
  margin: 0 auto;
}

.admin-detail-input {
  width: 900px;
  padding: 10px;
  font-size: 1.25rem;
  margin-bottom: 30px;
}

.admin-detail-btn {
  width: 1024px;
  margin-left: 55px;
}

.admin-content-btn {
  width: 1200px;
  margin: 0 55px;
}

.admin-detail-btn-box {
  margin-left: auto;
  float: right;
}

.admin-detail-btn-box > button {
  width: 75px;
  height: 40px;
  margin-left: 20px;
  border: none;
  border-radius: 5px;
  font-size: 1.25rem;
  font-weight: 500;
  color: #fff;
  transition: background 0.2s ease-in-out;
  cursor: pointer;
}

.admin-detail-update {
  background-color: #4973db;
}

.admin-detail-update:hover {
  background-color: #244497;
}

.admin-detail-delete {
  background-color: rgb(255, 123, 123);
}

.admin-detail-delete:hover {
  background-color: rgb(255, 0, 0);
}

.admin-product-file {
  margin-bottom: 30px;
}

.search-container {
  display: flex;
  width: 1200px;
  margin: 30px 0 20px 55px;
}

input[type="text"] {
  padding: 8px;
  border: solid 1px #000;
  border-radius: 4px;
  width: 100%;
  margin-right: 8px;
}

.search-container > button {
  background-color: #000;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px;
  cursor: pointer;
}

.inquiry {
  width: 700px;
  background-color: aliceblue;
  border: #244497 solid 1px;
}
.inquiry > div {
  margin: 20px;
}
.inquiry > textarea {
  width: 700px;
}
.caution {
  width: 656px;
  height: 150px;
  resize: vertical;
  background-color: #f1f1f1;
}
.inquiryform {
  margin-top: 20px;
}
.inquiryform > label {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.inquiryform > label > text {
  font-size: 16px;
  font-weight: bold;
  color: gray;
}
.inquiryform > label > input {
  flex: 1;
  height: 30px;
  border: none;
  padding-left: 10px;
  font-size: 16px;
}
.inquiryform > label > textarea {
  resize: none;
  width: 656px;
  height: 500px;
  padding: 20px;
  font-size: 16px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.summitButton {
  display: flex;
  justify-content: center;
}
.summitButton > button {
  background-color: #244497;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.summitButton > button:hover {
  background-color: #fff;
  color: #244497;
  transform: translateY(-2px);
  box-shadow: 0px 5px 10px rgba(36, 68, 151, 0.3);
}

.search-container {
  display: flex;
  align-items: center;
  width: 250px;
}

.search-container > input {
  padding: 8px;
  border: solid 1px #000;
  border-radius: 4px;
  width: 100%;
  margin-right: 8px;
}

.search-container > button {
  background-color: #000;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px;
  cursor: pointer;
}
