/* Default 1440px */
.page {
  width: 100%;
}
.pageBackgroundImage {
  position: fixed;
  z-index: 1;
  width: 100%;
  min-width: 1024px;
  height: 30vh;
}
.page-title {
  animation: fadeIn 2s;
  position: fixed;
  z-index: 2;
  margin-top: 13vh;
  width: 100%;
  min-width: 1024px;
  text-align: center;
  font-size: 60px;
  font-weight: 600;
  color: white;
}
.page-title > div {
  animation: fadeInUp 2s;

  font-size: 40px;
  margin-top: 10px;
}
.page-title > div > text {
  font-size: 45px;
  font-weight: 700;
  color: #ff7f00;
}
.pageContainer {
  width: 100%;
  min-width: 1024px;
  position: absolute;
  z-index: 2;
  margin-top: 30vh;
  background-color: white;
}
.pageRoute {
  width: 100%;
  min-width: 1024px;
  height: 24px;
  padding-top: 10px;
  display: flex;
  justify-content: center;
}
.pageRoute-1 {
  width: 1350px;
  display: flex;
  justify-content: flex-end;
  font-size: 18px;
  font-weight: 500;
  color: #244497;
}
.pageRoute-1 > a {
  font-size: 18px;
  font-weight: 500;
  color: #244497;
}
.pageContents {
  min-width: 900px;
  padding-top: 20px;
  padding-bottom: 50px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.navBox {
}
.contentsBox {
  width: 1200px;
  min-height: 700px;
}
.contents {
  display: flex;
  justify-content: center;
}
.contentImg {
  animation: fadeIn 2s;
  width: 100%;
}
.scroll__container {
  position: fixed;
  right: 5%;
  bottom: 5%;
}
#top {
  width: 60px;
  height: 60px;
  font-weight: bold;
  font-size: 15px;
  padding: 15px 10px;
  background-color: #244497;
  color: #fff;
  border: 2px solid #244497;

  outline: none;
  cursor: pointer;
}
#top:hover {
  background: aliceblue;
  color: #244497;
}

@media (max-width: 1440px) {
  .contentsBox {
    width: 1000px;
  }
  .pageRoute-1 {
    width: 1200px;
  }
}
@media (max-width: 1024px) {
  .contentsBox {
    width: 700px;
  }
  .navBox {
    display: none;
  }
  .pageRoute-1 {
    width: 900px;
  }
}

.page-box {
  width: 11900px;
  justify-content: center;
  margin: 0 auto;
  height: 100%;
}

.page-number {
  text-align: center;
  width: 30px;
  height: 30px;
  padding: 5px;
  border-radius: 10px;
  box-sizing: border-box;
  cursor: pointer;
  font-weight: 500;
  transition: background 0.2s ease-in-out;
}

.page-number:hover {
  background-color: #396df137;
}

.page-clicked {
  background-color: #244497;
  color: #fff;
}
