.inquiryInfoBox{
  width: 400px;
  margin-right: 20px;
}

.inquiryBox {
  width: 800px;
  height: 1020px;
  padding-top: 30px;
  padding-bottom: 20px;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  background-color: #f1f1f1;
}
textarea{
  resize: none;
}
textarea:focus {
  outline: none;
}
/* 체크박스 */
.inquiryBox > div > div > label {
  font-size: 14px;
}
/* 동의사항 */
.inquiryBox > div > textarea {
  white-space: pre-wrap;
  width: 650px;
  height: 150px;
  padding: 10px;
  margin-bottom: 10px;
  border: none;
  font-size: 12px;
}
/* 상호명 */
.inquiryBox > div > text {
  font-size: 14px;
  font-weight: 600;
  color: #244497;
}
/* Eng */
.inquiryBox > div > div > text {
  font-size: 14px;
  font-weight: 600;
  color: #244497;
}
.inquiryBox > div > input {
  width: 550px;
  height: 25px;
  border: none;
}
.inquiryBox > div > input:focus {
  outline: none !important;
  box-shadow: 0 0 5px #244497;
}
/* 내용 */
.inquiryBox > div > div > textarea {
  width: 650px;
  height: 500px;

  padding: 10px;
  border-color: #d5d5d5;
}
.inquiryBox > div > div > textarea::placeholder {
  font-size: 14px;
}

.inquiryBox>button{
  width: 100px;
  height: 30px;
  border: #244497;
  border-radius: 5%;
  background-color: #244497;
  color: white;

  font-size: 18px;
}

.inquiryBox>button:active{

  box-shadow: 1px 1px 0 rgb(0, 0, 0, 0.5);
  position: relative;
  top: 1px;
}
@media (max-width:1300px) {
  .inquiryInfoBox{
    display: none;
  }
}