.nav-container {
  width: 160px;
  text-align: center;
  border-radius: 5px;
  border: 1px solid #244497;
  overflow: hidden;
  box-sizing: border-box;
}

.nav-container nav {
  font-size: 1rem;
  padding: 10px 0;
  font-weight: 500;
}

.nav-content nav {
  cursor: pointer;
}

.nav-content nav:hover {
  background-color: aliceblue;
}

.nav-clicked {
  color: #244497;
  background-color:aliceblue;
}

.nav-header {
  background-color: #244497;
  color: #fff;
}
