/* KakaoMap 때문에 써둠  <a/> */
a {
  text-decoration: none;
  color: black;
}
.mapSize {
  width: 900px;
  height: 500px;
}
.addressText {
  animation: fadein 2s;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.addressTextTittle {
  font-size: 40px;
  font-weight: 700;
  color: #244497;
}
.addressText > div > text {
  font-size: 14px;
  font-weight: 500;
}
.addressText > div > text > text {
  font-size: 16px;
  font-weight: 700;
}
