.product-container {
  width: 1000px;
}
.product-box {
  width: 100%;
  height: 190px;
  display: flex;

  border: #244497 solid 1px;
  border-right: #244497 solid 2px;
  box-shadow: 3px 2px 5px rgb(196, 196, 196);
  cursor: pointer;
  margin-bottom: 30px;
}
.product-img {
  width: 250px;
  height: 190px;
  border-right: #244497 solid 1px;
}
.product-img img {
  width: 250px;
  height: 190px;
}
.product-title {
  width: 750px;
  height: 30px;

  padding-left: 15px;
  padding-right: 15px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  background-color: #244497;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  font-style: italic;
  box-sizing: border-box;
}
.product-content {
  width: 750px;
  height: 160px;
  background-color: #f5f6fa;
}
.product-detail {
  padding-top: 15px;
  padding-left: 15px;
  font-size: 14px;
  font-weight: 600;
}
.product-detail div {
  margin-bottom: 17px;
}
.product-detail div text {
  font-weight: 500;
  color: #777;
}
.product-drawing {
  text-align: center;
}
.product-attachment {
  width: 1200px;
  height: 200px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
}
.product-attachment > div {
  width: 1000px;
  height: 180px;
  padding: 20px;
  background-color: #f1f1f1;
}
.product-search {
  width: 252px;
  height: 40px;
  margin-bottom: 10px;
}
.product-search > input {
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
  font-size: 16px;
  border: 1px solid #dddddd;
  box-shadow: 1px 1px 1px rgb(196, 196, 196);
}
@media (max-width: 1440px) {
  .product-container {
    width: 900px;
  }
  .product-title {
    width: 650px;
  }
  .product-content {
    width: 650px;
  }
  .product-attachment {
    width: 1000px;
  }
  .product-attachment > div {
    width: 900px;
  }
}
@media (max-width: 1024px) {
  .product-attachment {
    width: 900px;
  }
  .product-attachment > div {
    width: 900px;
  }
}
