.cscenter{
    width: 1000px;
}
.cscenterText{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100px;
    padding-bottom: 30px;
}
.cscenterText-1{
    font-size: 30px;
    font-weight: 800;
}
.cscenterText-2{
    font-size: 18px;
}
.cscenterWrap{
    box-sizing: border-box;
    min-height: 500px;
    background-color: aliceblue;
}
.cscenterTitle{
    /* padding-left: 30px;
    padding-right: 50px; */
    height: 60px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #244497;
    color: #ffffff;
    font-size: 18px;
    font-weight: 600;
    align-items: center;
}
.cscenterTitle-1{
    display: flex;
    justify-content: center;
    width: 50px;
}
.cscenterTitle-2{
    width: 600px;
}
.cscenterTitle-3{
    display: flex;
    justify-content: center;
    width: 150px;
}

/* .cscenterTitle>div>div{
    width: 700px;
} */
.cscenterContent{
    padding-left: 30px;
    padding-right: 50px;
    padding-top: 5px;
    padding-bottom: 5px;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    font-weight: 500;
    border-bottom: solid 0.5px;
}
.cscenterContent>div>div{
    width: 700px;
}
.cscenterContent:hover{
    background-color: #ffffff;
}
.cscenterDetail{
    box-sizing: border-box;
    display: flex;
    padding: 30px;
    justify-content: center;
    min-height: 500px;
}
.cscenterDetail>div{
    width: 900px;
    padding: 30px;
    background-color: #ffffff;
}