.mr-0 {
  margin: 0 auto;
}

.flex {
  display: flex;
}

.f-30 {
  font-size: 30px;
}

.f-20 {
  font-size: 20px;
}

.fw-600 {
  font-weight: 600;
}

.bg-color {
  background-color: #244497;
}

.none {
  display: none;
}

body {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    transform: translateZ(0);
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes v1 {
  0% {
    top: 70px;
    opacity: 0;
  }

  100% {
    top: 0;
    opacity: 1;
  }
}
@keyframes v2 {
  0% {
    top: 185px;
    opacity: 0;
  }
  100% {
    top: 115px;
    opacity: 1;
  }
}
