.inspect-container {
    width: 1200px;
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.inspect-box {
    width: 400px;
    height: 400px;
    margin-bottom: 20px;

    display: flex;
    align-items: center;
    flex-direction: column;
}
.inspect-img-box {
    width: 300px;
    height: 350px;
    box-shadow: 1px 1px 5px rgb(196, 196, 196);
}
.inspect-img-box img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.inspect-text {
    margin-top: 10px;
    font-size: 20px;
    font-weight: 500;
}
@media (max-width:1440px) {
    .Certificate-container {
        width: 1000px;
    }
    .certificate-box {
        width: 330px;
        height: 480px;
        text-align: center;
    }
    .certificate-box>img {
        width: 300px;
    }
    .inspect-container{
        width: 1000px;
    }
    .inspect-box{
        width: 330px;
        height: 400px;
    }
}