.home {
  position: relative;
}
.homeimg {
  position: relative;
  height: 800px;
  min-width: 1024px;
  background: url(../asset/home.jpg) no-repeat center;
}
.homeimg > div {
  position: absolute;
  left: 50%;
  top: 40%;
  width: 900px;
  height: 175px;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
}
.homeimg > div > div {
  position: absolute;
  height: 100px;
  overflow: hidden;
  font-size: 90px;
  color: #244497;
  font-weight: 700;
  line-height: 100%;
  opacity: 0%;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.homeimg > div div.v1 {
  animation-name: v1;
  animation-duration: 1s;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
}
.homeimg > div span {
  position: absolute;
  opacity: 0%;

  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 30px;
  line-height: 61px;
  /* identical to box height */
  color: #ffffff;
}
.homeimg > div span.v2 {
  animation-name: v2;
  animation-duration: 1s;
  animation-delay: 1.8s;
  animation-fill-mode: forwards;
}
.wrap {
  min-width: 1024px;
  display: flex;
  justify-content: center;
}
.wrapBox {
  display: flex;
  width: 1400px;
  align-items: center;
  justify-content: space-between;
}
.wrapBox > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 320px;
  height: 300px;
  margin-bottom: 30px;
  background-color: aliceblue;
  border-radius: 10px;
}
.wrapBox > div > text {
  animation-name: v1;
  animation-duration: 1s;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
  font-size: 24px;
  font-weight: 700;
  opacity: 0;
  color: #244497;
}
.wrapContents {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 260px;
  animation-name: v2;
  animation-duration: 1s;
  animation-delay: 1.8s;
  animation-fill-mode: forwards;
  opacity: 0%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.wrapContents img {
  width: 300px;
  height: 260px;
}
.H-img {
  width: 300px;
  height: 260px;
}
.modal {
  z-index: 6;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 20px;
  box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.3);
}
.modal > div {
  width: 300px;
  height: 380px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
}
.modal h2 {
  font-size: 24px;
  margin-bottom: 20px;
  color: black;
}

.modal p {
  font-size: 18px;
  margin-bottom: 20px;
  color: black;
}

.modal button {
  bottom: 0px;
  width: 100%;
  padding: 10px 20px;
  border-radius: 5px;
  background-color: lightblue;
  color: white;
  font-size: 16px;
  cursor: pointer;
  border: none;
  outline: none;
  margin-top: 20px;
}

.modal button:hover {
  background-color: blue;
}
@media (max-width: 1024px) {
  .wrapBox {
    width: 800px;
    min-width: 800px;
    flex-wrap: wrap;
  }
}
