/* min-width:1920px 기준 */
.header{
  z-index: 10;
  width: 100%;
  min-width: 1024px;
  height: 70px;    
  display: flex;
  justify-content: center;
  position: fixed;
  background-color: white;
  background-color: rgba( 255, 255, 255 );
  border-bottom: 2px solid #244497;
}
.headerContainer-1{
  width: 1400px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.headerlogoContainer{
  display: flex;
  align-items: center;
}
.headerlogo{
  height: 40px;
  object-fit: contain;
}
ul,li{
  list-style: none;
  text-align: center;
}
.headerContainer-2{
  width: 780px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.headerContents{
}
.headerContentsTitle{
  width: 130px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: 600;
}
.dropMenu{
  display: none;
}
.dropMenu>li{
}
.dropMenuText{
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 16px;
  width: 130px;
  height: 40px;
}
.headerContentsTitle>button{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 22px;
  background-color: white;
  border:#d5d5d5 solid 1px;
}
.headerContents:hover >.dropMenu{
  display: flex;
  flex-direction: column;
  width: 130px;
  background-color: #244497;
}
.headerContentsTitle:hover{
  background-color: aliceblue;
}
.dropMenuText:hover{
  color: white;
  font-weight: 500;
}
@media (max-width:1440px) {
  .headerContainer-1{
    width: 1200px;
  }
  .headerContainer-2{
    width: 720px;
  }
  .headerContentsTitle{
    width: 120px;
  }
  .dropMenuText{
    width: 120px;
  }
  .headerContents:hover>.dropMenu{
    width: 120px;
  }
}
@media (max-width:1024px) {
  .headerContainer-1{
    width: 900px;
  }
  .headerContainer-2{
    width: 600px;
  }
  .headerContentsTitle{
    width: 100px;
  }
  .dropMenuText{
    width: 100px;
  }
  .headerContents:hover>.dropMenu{
    width: 100px;
  }
}